<template>

  <div>

    <v-row>
      <v-col class="col-12 col-md-6 col-lg-5">
        <data-card title="Active Calls">
          <v-row class="px-4 py-2 mb-0">
            <data-bit class="col-auto py-1" title="Live Now">1</data-bit>
            <data-bit class="col-auto py-1" title="Past Hour">
              <div>25</div>
              <div class="error--text font-weight-black" style="font-size:11px;line-height:160%"><v-icon x-small class="error--text mr-1">mdi-arrow-down-thick</v-icon>25%</div>
            </data-bit>
          </v-row>
          <v-divider/>
          <v-card-actions>
            <v-btn text to="/queue" color="primary">Go To Agents</v-btn>
          </v-card-actions>
        </data-card>
      </v-col>

      <v-col class="col-12 col-md-6 col-lg-5 mr-auto">
        <data-card title="Calls Awaiting Review">
          <v-row class="px-4 py-2 mb-0">
            <data-bit class="col-auto py-1" title="Urgent Issues"><span class="error--text">8</span></data-bit>
            <data-bit class="col-auto py-1" title="Total Queued">25</data-bit>
            <data-bit class="col-auto py-1" title="Completed">
              <div>75</div>
              <div class="success--text font-weight-black" style="font-size:11px;line-height:160%"><v-icon x-small class="success--text mr-1">mdi-arrow-up-thick</v-icon>75%</div>
            </data-bit>
          </v-row>
          <v-divider/>
          <v-card-actions>
            <v-btn text to="/queue" color="primary">Fix Issues In Review Queue</v-btn>
          </v-card-actions>
        </data-card>
      </v-col>

    </v-row>

    <v-row>

      <v-col class="col-12 col-md-6 col-lg-5">
        <data-card title="Agent Usage">
          <v-row class="px-4 py-2 mb-0">
            <data-bit class="col-auto py-1" title="Agents Available">10</data-bit>
            <data-bit class="col-auto py-1" title="Time Spent At Capacity">
              <div>25</div>
              <div class="error--text font-weight-black" style="font-size:11px;line-height:160%"><v-icon x-small class="error--text mr-1">mdi-arrow-down-thick</v-icon>44%</div>
            </data-bit>
            <div class="col-12 pt-0 pb-1 text-caption">Suggestion: <a>Increase Agent Count to 25</a></div>
          </v-row>

          <v-divider/>

          <apexchart :options="agentOptions" :series="agentSeries" height="160px" class="py-2"></apexchart>

          <v-divider/>

          <v-card-actions>
            <v-btn text to="/queue" color="primary">Go To Agents</v-btn>
          </v-card-actions>
        </data-card>
      </v-col>

      <v-col class="col-12 col-md-6 col-lg-5 mr-auto">
        <data-card title="Source Distribution">

          <apexchart :options="sourceOptions" :series="sourceSeries" height="270px"></apexchart>

          <v-divider/>

          <v-card-actions>
            <v-btn text to="/queue" color="primary">Go To Source Performance</v-btn>
          </v-card-actions>
        </data-card>
      </v-col>

    </v-row>

    <v-row>

      <v-col class="col-12 col-lg-10">
        <data-card title="Revenue Trends">

          <apexchart :options="trendsOptions" :series="trendsSeries" height="330px"></apexchart>

          <v-divider/>

          <v-card-actions>
            <v-btn text to="/queue" color="primary">Go To Reports</v-btn>
          </v-card-actions>
        </data-card>
      </v-col>

    </v-row>

  </div>

</template>

<script>

import DataBit from '@/components/DataBit'
import DataCard from '@/components/DataCard'

const sheetOptions = {
  legend: {
    show: false
  },
  colors: [
    '#A3A0FB',
    '#6fd8f8',
    '#a0de78',
    '#facd6d',
    '#FF8373',
    '#ff9dea'
  ]
}

const chartOptions = {
  animations: {
    enabled: true,
    speed: 400,
    animateGradually: false
  },
  toolbar: {
    show: false
  }
}

const textColor = '#333'

export default {
  name: 'Dashboard',
  components: {
    DataBit,
    DataCard
  },

  data: () => ({
    agentSeries: [90],
    agentOptions: {
      labels: ['Capacity'],
      ...sheetOptions,
      chart: {
        type: 'radialBar',
        ...chartOptions,
        parentHeightOffset: 0
      },
      plotOptions: {
        radialBar: {
          hollow: {
            size: '99%'
          },
          track: {
            margin: -10
          },
          dataLabels: {
            name: {
              show: true,
              fontSize: '14px',
              offsetY: -5
            },
            value: {
              show: true,
              color: textColor,
              fontSize: '16px',
              offsetY: 5
            }
          }
        }
      }
    },

    sourceSeries: [44060, 39070, 34054, 23090, 13090],
    sourceOptions: {
      labels: ['Source A', 'Source B', 'Source C', 'Source D', 'Source E'],
      ...sheetOptions,
      chart: {
        type: 'donut',
        ...chartOptions
      },
      dataLabels: {
        enabled: false
      },
      plotOptions: {
        pie: {
          expandOnClick: false,
          donut: {
            labels: {
              show: true,
              total: {
                show: true,
                label: 'Revenue',
                fontSize: '14px',
                color: textColor,
                formatter: ({ globals }) => `$${new Intl.NumberFormat().format(globals.seriesTotals.reduce((a, b) => a + b, 0))}`
              },
              value: {
                show: true,
                color: textColor,
                fontSize: '14px',
                offsetY: 0,
                formatter: (value) => `$${new Intl.NumberFormat().format(value)}`
              }
            }
          }
        }
      }
    },

    trendsSeries: [{
      name: 'Source A',
      data: [16000, 8000, 12000, 2000, 18000, 23000, 10000]
    }, {
      name: 'Source B',
      data: [20100, 5000, 15000, 12000, 16000, 10000, 5000]
    }],
    trendsOptions: {
      ...sheetOptions,
      chart: {
        type: 'area',
        ...chartOptions
      },
      markers: {
        size: 4,
        strokeWidth: 2
      },
      dataLabels: {
        enabled: false
      },
      stroke: {
        curve: 'smooth',
        width: 2
      },
      fill: {
        type: 'gradient',
        gradient: {

          opacityFrom: 0.5,
          opacityTo: 0.2
        }
      },
      xaxis: {
        type: 'datetime',
        categories: ['2018-09-01', '2018-10-01', '2018-11-01', '2018-12-01', '2019-01-01', '2019-02-01', '2019-03-01'],
        labels: {
          show: true,
          style: {
            colors: '#aaa'
          }
        }
      },
      yaxis: {
        tickAmount: 5,
        labels: {
          show: true,
          style: {
            colors: '#aaa'
          },
          formatter: (num) => `$${Math.abs(num) > 999 ? Math.sign(num) * ((Math.abs(num) / 1000).toFixed(1)) + 'k' : Math.sign(num) * Math.abs(num)}`
        }
      },
      grid: {
        show: true,
        xaxis: {
          lines: {
            show: true
          }
        },
        yaxis: {
          lines: {
            show: true
          }
        }
      },
      tooltip: {
        x: {
          format: 'dd MMM yyyy'
        }
      }
    }

  })
}
</script>

<style lang="sass">

</style>
